<template>
  <div>
    <!-- 添加角色按钮 -->
    <el-button size="mini" type="danger" style="margin:10px 20px" @click="addRole('show')">直接数据类型</el-button>

    <!-- 元素列表 -->
    <el-table :data="list.items" v-loading="loading.items">
      <el-table-column label="类型名称" :show-overflow-tooltip="true" prop="name"></el-table-column>
      <el-table-column label="描述" :show-overflow-tooltip="true" prop="dataTypeDesc"></el-table-column>

      <!-- 编辑 删除 -->
      <el-table-column label="操作" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="addItem('edit',scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="delItem(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>

    </el-table>

    <!-- 分页 -->
    <el-pagination :page-size="page.pageSize" :current-page="page.pageIndex" :total="page.total" @current-change="pageChange"></el-pagination>

    <!-- 编辑或添加角色弹窗 -->
    <el-dialog :visible.sync="show.addRole" title="编辑/新建角色">
      <el-form abel-position="left">
        <el-form-item label="角色名:">
          <el-input v-model.trim="ed.role.name"></el-input>
        </el-form-item>

        <el-form-item label="具有 permission:">
          <template>
            <div>
              <el-checkbox-group v-model="ed.role.permission">
                <el-checkbox v-for="(r,ri) in list.permission" :label="r.id" :key="ri">{{r.name}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </template>
        </el-form-item>
      </el-form>

      <el-row type="flex" justify="center">
        <el-col :span="5">
          <el-button type="primary" @click="addRole('confirm')">确定</el-button>
        </el-col>
        <el-col :span="5">
          <el-button>取消</el-button>
        </el-col>

      </el-row>

    </el-dialog>

  </div>
</template>

<script>
let that = "";
export default {
  mounted() {
    that = this;
    this.reloadItems();
  },
  data() {
    return {
      loading: {
        items: false
      },
      type: "datasetType",
      ed: {
        //    被添加或者编辑的角色
        role: {
          id: "",
          name: "",
          permission: []
        }
      },
      show: {
        addRole: false
      },
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0
      },
      list: {
        items: []
      }
    };
  },
  methods: {
    //   删除角色
    delItem(id) {
      that
        .$confirm("确定要删除吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
        .then(_ => {
          axios.get(`/dataset/delDatasetType?id=${id}`).then(r => {
            if (r.code == 200) {
              that.$message({
                message: "删除成功",
                type: "success"
              });
              that.reloadItems();
            }
          });
        });
    },
    //   改变分页时刷新数据
    pageChange(p) {
      that.page.pageIndex = p;
      that.reloadItems();
    },
    //   添加或者编辑角色相关方法
    addItem(t, item) {
      // 如果是显示添加面板
      if (t == "show") {
        // 先清空数据
        that.addItem("close");
        that.show.addItem = true;
        that.reloadItems();
      }

      // 如果是 编辑 面板
      if (t == "edit") {
        // 先清空数据
        that.show.addItem = true;
        that.ed.datasetType = {
          name: item.item,
          dataTypeDesc: item.dataTypeDesc
        }
      }

      // 如果是关闭添加面板
      if (t == "close") {
        that.show.addItem = false;
        that.ed.item = {};
      }

      //   如果是确定添加
      if (t == "confirm") {
        axios
          .get("/humanRole/addRoleBack", {
            params: {
              id: that.ed.role.id,
              roleName: that.ed.role.name,
              permissionIds: that.ed.role.permission.join(",")
            }
          })
          .then(r => {
            that.addRole("close");
            if (r.code == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.reloadRoles();
            }
          });
      }
    },

    // 刷新所有item
    reloadItems() {
      let url = "";
      let that = this;
      that.loading.items = true;
      let para = {
        pageIndex: that.page.pageIndex,
        pageSize: that.page.pageSize
      };

      // 如果是数据类型
      if (that.type == "datasetType") {
        url = "/dataset/getDatasetTypes";
      }

      axios.get(url).then(r => {
        that.loading.items = false;

        if (r.code == 200) {
          // 如果是数据类型
          if (that.type == "datasetType") {
            that.list.items = r.data.content;
            that.page.total = r.data.totalElements;
          }
        }
      });
    }
  }
};
</script>

<style>
</style>



