<template>
  <div>

    <el-button size="mini" type="danger" style="margin:10px 20px" @click="addUser('show')">直接添加用户</el-button>
    <el-table :data="list.users">
      <el-table-column label="用户名" :show-overflow-tooltip="true" prop="username"></el-table-column>
      <el-table-column label="昵称" :show-overflow-tooltip="true" prop="nickname"></el-table-column>
      <el-table-column label="邮箱" :show-overflow-tooltip="true" prop="email"></el-table-column>
      <el-table-column label="角色" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-tag v-for="(r,ri) in scope.row.roles" :key="ri" size="mini" style="margin:3px">
            {{r.roleName}}
          </el-tag>
        </template>
      </el-table-column>

      <!-- 编辑 删除 -->
      <el-table-column label="操作" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="addUser('edit',scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="delUser(scope.row)">删除</el-button>
        </template>
      </el-table-column>

    </el-table>

    <!-- 分页 -->
    <el-pagination :page-size="page.pageSize" :current-page="page.pageIndex" :total="page.total" @current-change="pageChange"></el-pagination>

    <!-- 编辑或添加用户弹窗 -->
    <el-dialog :visible.sync="show.addUser" title="编辑/新建用户">
      <el-form>
        <el-form-item label="用户名">
          <template>
            <el-input v-model.trim="ed.user.username"></el-input>
          </template>
        </el-form-item>
        <el-form-item label="用户昵称">
          <template>
            <el-input v-model.trim="ed.user.nickname"></el-input>
          </template>
        </el-form-item>
        <el-form-item label="用户密码">
          <template>
            <el-input v-model.trim="ed.user.password"></el-input>
          </template>
        </el-form-item>
        <el-form-item label="用户邮箱">
          <template>
            <el-input v-model.trim="ed.user.email"></el-input>
          </template>
        </el-form-item>
        <el-form-item label="具有角色">
          <template>
            <div style="margin: 15px 0;"></div>
            <el-checkbox-group v-model="ed.user.roles">
              {{list.roles}}
              <el-checkbox v-for="(r,ri) in list.roles" :label="r.id" :key="ri">{{r.roleName}}</el-checkbox>
            </el-checkbox-group>
          </template>
        </el-form-item>
      </el-form>

      <el-row type="flex" justify="center">

        <el-col :span="5">
          <el-button type="primary" @click="addUser('confirm')">确定</el-button>
        </el-col>
        <el-col :span="5">
          <el-button>取消</el-button>
        </el-col>

      </el-row>

    </el-dialog>

  </div>
</template>

<script>
let that = "";
export default {
  created() {
    that = this;
    this.reloadUsers();
  },
  data() {
    return {
      ed: {
        //    被添加或者编辑的用户
        user: {
          username: "",
          nickname: "",
          password: "",
          email: "",
          roles: []
        }
      },
      show: {
        addUser: false
      },
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0
      },
      list: {
        users: [],
        roles: []
      }
    };
  },
  methods: {
    //   删除用户
    delUser(user) {
      that
        .$confirm("确定要删除吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
        .then(_ => {
          axios.get(`/human/delBack?id=${user.id}`).then(r => {
            if (r.code == 200) {
              that.$message({
                message: "删除成功",
                type: "success"
              });
              that.reloadUsers();
            }
          });
        });
    },
    //   改变分页时刷新数据
    pageChange(p) {
      that.page.pageIndex = p;
      that.reloadUsers();
    },
    //   添加或者编辑用户相关方法
    addUser(t, item) {
      // 如果是显示添加面板
      if (t == "show") {
        that.show.addUser = true;
        
        that.reloadRoles();
      }

      // 如果是 编辑
      if (t == "edit") {
        that.reloadRoles();
        that.show.addUser = true;
        that.ed.user.id = item.id;
        that.ed.user.username = item.username;
        that.ed.user.nickname =item.nickname;
        that.ed.user.password = item.password;
        that.ed.user.email = item.email;
        that.ed.user.roles = item.roles.map(e=>{return e.id});
      }

      // 如果是关闭添加面板
      if (t == "close") {
        that.show.addUser = false;
        that.ed.user.id = ""
        that.ed.user.username = "";
        that.ed.user.nickname = "";
        that.ed.user.password = "";
        that.ed.user.email = "";
        that.ed.user.roles = [];
      }

      //   如果是确定添加
      if (t == "confirm") {
        axios
          .get("/human/addHumanFromBack", {
            params: {
              id:that.ed.user.id,
              username: that.ed.user.username,
              nickname: that.ed.user.nickname,
              raw_password: that.ed.user.password,
              email: that.ed.user.email,
              roleIds: that.ed.user.roles.join(",")
            }
          })
          .then(r => {
            that.addUser("close");
            if (r.code == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.reloadUsers();
            }
          });
      }
    },

    // 刷新所有用户
    reloadUsers() {
      axios.get("/human/backList").then(r => {
        if (r.code == 200) {
          that.list.users = r.data;
        }
      });
    },

    // 刷新所有角色
    reloadRoles() {
      axios.get("/humanRole/get").then(r => {
        if (r.code == 200) {
          that.list.roles = r.data.content;
        }
      });
    }
  }
};
</script>

<style>
</style>



