<template>
  <div>

    <!-- 直接添加  permission 按钮 -->
    <el-button size="mini" type="danger" style="margin:10px 20px" @click="addPermission('show')">直接添加permission</el-button>

    <!-- permission 列表 -->
    <el-table :data="list.users">
      <el-table-column label="permission名称" :show-overflow-tooltip="true" prop="name"></el-table-column>
      <el-table-column label="前端允许访问地址" :show-overflow-tooltip="true" prop="frontUrls"></el-table-column>
      <el-table-column label="允许访问接口" :show-overflow-tooltip="true" prop="backUrls"></el-table-column>

      <!-- 编辑 删除 -->
      <el-table-column label="操作" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="addPermission('edit',scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="delPermission(scope.row)">删除</el-button>
        </template>
      </el-table-column>

    </el-table>

    <!-- 分页 -->
    <el-pagination :page-size="page.pageSize" :current-page="page.pageIndex" :total="page.total" @current-change="pageChange"></el-pagination>

    <!-- 编辑或添加用户弹窗 -->
    <el-dialog :visible.sync="show.addPermission" title="编辑/新建 permission">
      <el-form>
        <el-form-item label="名称">
          <template>
            <el-input v-model.trim="ed.permission.name"></el-input>
          </template>
        </el-form-item>
        <el-form-item label="前端允许访问地址">
          <template>
            <el-input v-model.trim="ed.permission.frontUrls" type="textarea"></el-input>
          </template>
        </el-form-item>
        <el-form-item label="允许访问接口">
          <template>
            <el-input v-model.trim="ed.permission.backUrls" type="textarea"></el-input>
          </template>
        </el-form-item>
      </el-form>

      <el-row type="flex" justify="center">

        <el-col :span="5">
          <el-button type="primary" @click="addPermission('confirm')">确定</el-button>
        </el-col>
        <el-col :span="5">
          <el-button>取消</el-button>
        </el-col>

      </el-row>

    </el-dialog>

  </div>
</template>

<script>
let that = "";
export default {
  created() {
    that = this;
    this.reloadPermissions();
  },
  data() {
    return {
      ed: {
        //    被添加或者编辑的用户
        permission: {
          id:"",
          name: "",
          frontUrls: "",
          backUrls: ""
        }
      },
      show: {
        addPermission: false
      },
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0
      },
      list: {
        users: []
      }
    };
  },
  methods: {
    //   删除 Permission
    delPermission(permission) {
      that
        .$confirm("确定要删除吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
        .then(_ => {
          axios.get(`/permission/delBack?id=${permission.id}`).then(r => {
            if (r.code == 200) {
              that.$message({
                message: "删除成功",
                type: "success"
              });
              that.reloadPermissions();
            }
          });
        });
    },
    //   改变分页时刷新数据
    pageChange(p) {
      that.page.pageIndex = p;
      that.reloadPermissions();
    },
    //   添加或者编辑用户相关方法
    addPermission(t, item) {
      // 如果是显示添加面板
      if (t == "show") {
        that.show.addPermission = true;
        that.reloadRoles();
      }

      // 如果是关闭添加面板
      if (t == "close") {
        that.show.addPermission = false;
        that.ed.permission.id = "";
        that.ed.permission.name = "";
        that.ed.permission.frontUrls = "";
        that.ed.permission.backUrls = "";
      }

      // 如果是 编辑
      if (t == "edit") {
        that.show.addPermission = true;
        that.ed.permission.id = item.id;
        that.ed.permission.name = item.name;
        that.ed.permission.frontUrls = item.frontUrls;
        that.ed.permission.backUrls = item.backUrls;
      }

      //   如果是确定添加
      if (t == "confirm") {
        // 根据是否有id判断是添加还是更新
        let url = "/permission/add";
        let para = {
          name: that.ed.permission.name,
          frontUrls: that.ed.permission.frontUrls,
          backUrls: that.ed.permission.backUrls
        };
        if (that.ed.permission.id) {
          url = "/permission/update";
          para.id = that.ed.permission.id;
        }
        axios
          .get(url, {
            params: para
          })
          .then(r => {
            that.addPermission("close");
            if (r.code == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.reloadPermissions();
            }
          });
      }
    },

    // 刷新所有 Permission
    reloadPermissions() {
      axios.get("/permission/getPermissionBackWithRoleName").then(r => {
        if (r.code == 200) {
          that.list.users = r.data;
          that.page.total = r.additonalData.total;
        }
      });
    },

    // 刷新所有角色
    reloadRoles() {
      axios.get("/humanRole/get").then(r => {
        if (r.code == 200) {
          that.list.roles = r.data.content;
        }
      });
    }
  }
};
</script>

<style>
</style>



