<template>
  <div>
    <h1>后台管理</h1>

    <el-tabs tab-position="left" v-model="tab">

      <!-- 用户管理 -->
      <el-tab-pane label="用户管理" name="user">
        <back-users v-if="tab=='user'"></back-users>
      </el-tab-pane>

      <!-- 角色管理 -->
      <el-tab-pane label="角色管理" name="role">
        <back-roles v-if="tab=='role'"></back-roles>
      </el-tab-pane>

      <!-- permission 管理 -->
      <el-tab-pane label="permission管理" name="permission">
        <back-permissions v-if="tab=='permission'"></back-permissions>
      </el-tab-pane>

      <!-- 数据类型管理 -->
      <el-tab-pane label="数据类型管理" name="datasetType">
        <back-dataset-type v-if="tab=='datasetType'"></back-dataset-type>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import backUsers from "./back/backUsers";
import backRoles from "./back/backRoles";
import backPermissions from "./back/backPermissions";
import backDatasetType from "./back/backDatasetType";

let that = "";
export default {
  components: {
    backUsers,
    backRoles,
    backPermissions,
    backDatasetType
  },
  data() {
    return {
      tab: "user"
    };
  },
  methods: {}
};
</script>

<style>
</style>



