<template>
  <div>
    <!-- 添加角色按钮 -->
    <el-button size="mini" type="danger" style="margin:10px 20px" @click="addRole('show')">直接添加角色</el-button>

    <!-- 角色列表 -->

    <el-table :data="list.roles">
      <el-table-column label="角色名" :show-overflow-tooltip="true" prop="name"></el-table-column>
      <el-table-column label="拥有权限" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-tag v-for="(p,pi) in scope.row.permissions" :key="pi" size="mini" style="margin:3px">
            {{p.name}}
          </el-tag>
        </template>
      </el-table-column>

      <!-- 编辑 删除 -->
      <el-table-column label="操作" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="addRole('edit',scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="delRole(scope.row)">删除</el-button>
        </template>
      </el-table-column>

    </el-table>

    <!-- 分页 -->
    <el-pagination :page-size="page.pageSize" :current-page="page.pageIndex" :total="page.total" @current-change="pageChange"></el-pagination>

    <!-- 编辑或添加角色弹窗 -->
    <el-dialog :visible.sync="show.addRole" title="编辑/新建角色">
      <el-form abel-position="left">
        <el-form-item label="角色名:">
          <el-input v-model.trim="ed.role.name"></el-input>
        </el-form-item>

        <el-form-item label="具有 permission:">
          <template>
            <div>
              <el-checkbox-group v-model="ed.role.permission">
                <el-checkbox v-for="(r,ri) in list.permission" :label="r.id" :key="ri">{{r.name}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </template>
        </el-form-item>
      </el-form>

      <el-row type="flex" justify="center">
        <el-col :span="5">
          <el-button type="primary" @click="addRole('confirm')">确定</el-button>
        </el-col>
        <el-col :span="5">
          <el-button>取消</el-button>
        </el-col>

      </el-row>

    </el-dialog>

  </div>
</template>

<script>
let that = "";
export default {
  mounted() {
    that = this;
    this.reloadRoles();
  },
  data() {
    return {
      ed: {
        //    被添加或者编辑的角色
        role: {
          id: "",
          name: "",
          permission: []
        }
      },
      show: {
        addRole: false
      },
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0
      },
      list: {
        roles: [],
        permission: []
      }
    };
  },
  methods: {
    //   删除角色
    delRole(role) {
      that
        .$confirm("确定要删除吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消"
        })
        .then(_ => {
          axios.get(`/humanRole/delBack?id=${role.roleId}`).then(r => {
            if (r.code == 200) {
              that.$message({
                message: "删除成功",
                type: "success"
              });
              that.reloadRoles();
            }
          });
        });
    },
    //   改变分页时刷新数据
    pageChange(p) {
      that.page.pageIndex = p;
      that.reloadRoles();
    },
    //   添加或者编辑角色相关方法
    addRole(t, item) {
      // 如果是显示添加面板
      if (t == "show") {
        // 先清空数据
        that.addRole("close");
        that.show.addRole = true;
        that.reloadPermissions();
      }

      // 如果是 编辑 面板
      if (t == "edit") {
        // 先清空数据
        that.show.addRole = true;
        that.ed.role.id = item.roleId;
        that.ed.role.name = item.name;
        that.ed.role.permission = item.permissions.map(e => {
          return e.id;
        });

        that.reloadPermissions();
      }

      // 如果是关闭添加面板
      if (t == "close") {
        that.show.addRole = false;

        that.ed.role.id = "";
        that.ed.role.name = "";
        that.ed.role.permission = [];
      }

      //   如果是确定添加
      if (t == "confirm") {
        axios
          .get("/humanRole/addRoleBack", {
            params: {
              id: that.ed.role.id,
              roleName: that.ed.role.name,
              permissionIds: that.ed.role.permission.join(",")
            }
          })
          .then(r => {
            that.addRole("close");
            if (r.code == 200) {
              that.$message({
                message: "添加成功",
                type: "success"
              });
              that.reloadRoles();
            }
          });
      }
    },

    // 刷新所有角色
    reloadRoles() {
      axios.get("/humanRole/getRoleBackWithPermission").then(r => {
        if (r.code == 200) {
          that.list.roles = r.data;
          that.page.total = r.additonalData.total;
        }
      });
    },

    // 刷新所有 permission
    reloadPermissions() {
      axios.get("/permission/getPermissionBackWithRoleName").then(r => {
        if (r.code == 200) {
          that.list.permission = r.data;
        }
      });
    }
  }
};
</script>

<style>
</style>



